import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { checkMembership } from '../services/api';

export default function MemberCheck() {
  const [nom, setNom] = useState('');
  const [prenom, setPrenom] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleVerification = async (e) => {
    e.preventDefault();
    
    // Validation des champs côté client
    if (!nom.trim() || !prenom.trim()) {
      setError("Veuillez entrer un nom et prénom valides");
      return;
    }
    
    setLoading(true);
    setError('');
    
    try {
  const result = await checkMembership(nom, prenom);
  console.log("Réponse complète:", result); // Vérifiez cette sortie
  
  if (result.isPaid && result.success) {
    navigate('/confirmation');
  } else {
    setError(result.message || "Adhésion non valide");
  }
} catch (err) {
  setError(err.message);
}
  };

  return (
    <div className="member-check">
      <h2>Vérification d'adhésion</h2>
      <form onSubmit={handleVerification}>
        <div className="form-group">
          <label htmlFor="nom">Nom</label>
          <input
            id="nom"
            type="text"
            placeholder="Nom"
            value={nom}
            onChange={(e) => setNom(e.target.value)}
            disabled={loading}
            required
          />
        </div>
        
        <div className="form-group">
          <label htmlFor="prenom">Prénom</label>
          <input
            id="prenom"
            type="text"
            placeholder="Prénom"
            value={prenom}
            onChange={(e) => setPrenom(e.target.value)}
            disabled={loading}
            required
          />
        </div>
        
        <button 
          type="submit" 
          disabled={loading}
          className={loading ? 'loading-button' : ''}
        >
          {loading ? 'Vérification en cours...' : 'Vérifier'}
        </button>
        
        {error && (
          <div className="error-message">
            {error}
          </div>
        )}
      </form>
      
      <button 
        onClick={() => navigate('/')}
        className="back-button"
      >
        Retour à l'accueil
      </button>
    </div>
  );
}
